// @ts-check

import { Lib, V } from "../modules/index.js"
import { iterate } from "../modules/itertools.js"
import { Map2d } from "../modules/map2d.js"
import { t } from "../modules/parser.js"

export const useExample = false

export const exampleInput = `\
p=0,4 v=3,-3
p=6,3 v=-1,-3
p=10,3 v=-1,2
p=2,0 v=2,-1
p=0,0 v=1,3
p=3,0 v=-2,-2
p=7,6 v=-1,-3
p=3,0 v=-1,-2
p=9,3 v=2,3
p=7,3 v=-1,2
p=2,4 v=2,-3
p=9,5 v=-3,-3`

/** @typedef {ReturnType<typeof parseInput>} InputType */

const lineParser = t.tpl`p=${"pos|vec"} v=${"vel|vec"}`
export const parseInput = t.arr(lineParser).parse

/**
 * @param {V.Vec2} pos
 * @param {V.Vec2} vel
 * @param {number} t
 * @param {number} width
 * @param {number} height
 */
export function calc(pos, vel, t, width, height) {
	const target = V.add(pos, V.scale(vel, t))
	return V.vec(Lib.mod(target[0], width), Lib.mod(target[1], height))
}

/**
 * @param {V.Vec2[]} positions
 * @param {number} width
 * @param {number} height
 */
function printDebug(positions, width, height, chWidth = 1) {
	const debugMap = new Map2d()
	for (const pos of positions) {
		debugMap.set(pos, (debugMap.get(pos) ?? 0) + 1)
	}
	const str = debugMap.toString({
		topLeftPos: V.vec(0, 0),
		botRightPos: V.vec(width - 1, height - 1),
		valToString: (v) => (v ? "#".repeat(chWidth) : ".".repeat(chWidth)),
	})
	return str
}

/**
 * @param {InputType} input
 */
export function part1(input) {
	const width = 101
	const height = 103

	const cx = (width - 1) / 2
	const cy = (height - 1) / 2

	return input
		.values()
		.map((r) => calc(r.pos, r.vel, 100, width, height))
		.filter((p) => V.x(p) !== cx && V.y(p) !== cy)
		.groupBy((pos) => {
			if (V.x(pos) < cx && V.y(pos) < cy) return 1
			if (V.x(pos) > cx && V.y(pos) < cy) return 2
			if (V.x(pos) < cx && V.y(pos) > cy) return 3
			return 4
		})
		.values()
		.map((x) => x.length)
		.multiply()
}

/**
 * @param {string} str
 * @param {string} pattern
 */
function stringIncludesPattern(str, pattern) {
	const lines = str.split("\n")
	const patternLines = pattern.split("\n")

	const res = lines
		.values()
		.windowed(patternLines.length)
		.find((window) => window.every((line, i) => line.includes(patternLines[i])))

	return !!res
}

/**
 * @param {InputType} robots
 * @param {string} pattern
 */
export function robotsMatchesPattern(robots, pattern) {
	const str = printDebug(
		robots.map((r) => r.pos),
		101,
		103,
		1,
	)
	return stringIncludesPattern(str, pattern)
}

/**
 * @param {number} width
 * @param {number} height
 */
export function next(width, height) {
	return (/** @type {InputType} */ r) =>
		r.map(({ pos, vel }) => ({ pos: calc(pos, vel, 1, width, height), vel }))
}

export const pattern = `\
....#....
...###...
..#####..
.#######.
`

/**
 * @param {InputType} input
 */
export function part2(input) {
	const w = 101
	const h = 103

	return iterate(input, next(w, h))
		.takeUntil((r) => robotsMatchesPattern(r, pattern))
		.count()
}
